/* Import Fonts */

@import url('https://fonts.googleapis.com/css?family=Playfair+Display');
@font-face{
    font-family: 'Horatio';
    src: url('../fonts/LT_55887.ttf') format('truetype');
}

.container {
    background: #f8f8f8;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    background-size: 100% auto;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    width: 100vw;
    max-width: 1300px;
    letter-spacing: 0.5px;
    font-family: 'Playfair Display';
    font-size: 30px;
    padding: 0 28px;
}

.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
    padding: 12px;
    width: 100%;
    height: 100px;
    max-width: 1300px;
    text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.17);
}


@media screen and (max-width: 600px) {
    .navbar {
        padding: 0px;
    } 
}

.leftNav {
    display: -ms-flexbox;
    display: flex;
    font-size: 32px;
    font-weight: 100;
    /*text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.17);*/
    padding: 12px 8px 8px 12px;
    font-family: 'Playfair Display';
}

@media screen and (max-width: 600px) {
    .leftNav {
        padding: 1px 12px 22px 8px;
    } 
}

.rightNav {
    display: -ms-flexbox;
    display: flex;
    font-size: 20px;
    font-weight: 100;
    text-shadow: 1px 0.5px 0.5px rgba(0, 0, 0, 0.1);
    padding: 12px 16px 8px 8px;
    letter-spacing: 0.5px;
    font-family: 'Playfair Display';
}

@media screen and (max-width: 600px) {
    .rightNav {
        padding: 1px 8px 24px 12px;
    } 
}

.home {
    -ms-flex-item-align: center;
        align-self: center;
    text-align: center;
    letter-spacing: 0.5px;
    font-family: 'Playfair Display';
    font-size: 30px;
    max-width: 900px;
    padding: 0 60px 140px;
}

.aboutPage {
    width: 100%;
}

.contactPage {
    width: 100%;
}

.about {
    padding: 0 15px;
}

.contact {
    padding: 0 10px;
}

a {
    text-decoration: none;
    -webkit-transition: text-shadow 200ms;
    -o-transition: text-shadow 200ms;
    transition: text-shadow 200ms;
    -webkit-transition-timing-function: ease-in;
         -o-transition-timing-function: ease-in;
            transition-timing-function: ease-in;
}

a:link {
    color: black;
}

a:visited {
    color: black;
}

a:hover {
    color: black;
    text-shadow: 3px 2px 1.5px rgba(0, 0, 0, 0.20);
}

a:active {
    color: black;
}


